<template>
  <v-app-bar class="navigation">
    <v-btn :to="{ name: 'Home' }" icon>
      <v-icon>mdi-home</v-icon>
    </v-btn>
    <div>{{ language === 'ja' ? '時計' : 'Clock' }}</div>
    <v-spacer></v-spacer>
    <div class="buttons">
      <v-btn
        :color="language === 'ja' ? 'primary' : ''"
        size="x-large"
        @click="language === 'ja' ? null : toggleLanguage()"
        >日本語</v-btn
      >
      <v-btn
        :color="language === 'en' ? 'primary' : ''"
        size="x-large"
        @click="language === 'en' ? null : toggleLanguage()"
        >English</v-btn
      >
    </div>
  </v-app-bar>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'Navigation',
  data: () => ({}),
  computed: {
    ...mapGetters(['language'])
  },
  mounted () {},
  methods: {
    ...mapMutations([
      'toggleLanguage' // map `this.incrementBy(amount)` to `this.$store.commit('incrementBy', amount)`
    ])
  }
}
</script>

<style lang="scss">
.navigation {
  z-index: 9 !important;
}
.buttons {
  display: flex;
  gap: 32px;
}
</style>
