<template>
  <div
    @click="setTime"
    class="clock_container"
    :class="{
      'clock_container-isMoving': movingHand,
    }"
  >
    <div
      ref="clock"
      class="clock"
      :class="[edit ? 'clock-edit' : 'clock-view']"
    >
      <div
        class="clock_minutes_container"
        :class="{ 'clock_minutes_container-view': minutes }"
      >
        <div
          v-for="i of 60"
          :key="i"
          class="clock_minutes"
          :style="{ transform: 'rotate(' + (i - 1) * 6 + 'deg)' }"
        >
          <div
            class="clock_minutes_number"
            :class="{ 'clock_minutes_number-long': !((i - 1) % 5) }"
            :style="{ transform: 'rotate(' + (i - 1) * -6 + 'deg)' }"
          >
            {{ i - 1 }}
          </div>
        </div>
      </div>
      <div class="clock_check_container">
        <div
          v-for="i of 60"
          :key="i"
          class="clock_check"
          :class="{ 'clock_check-long': !((i - 1) % 5) }"
          :style="{ transform: 'rotate(' + (i - 1) * 6 + 'deg)' }"
        ></div>
      </div>
      <div
        v-for="i of 12"
        :key="i"
        class="clock_time"
        :class="[edit ? 'clock_time-edit' : 'clock_time-view']"
        :style="{ transform: 'rotate(' + (i - 1) * 30 + 'deg)' }"
      >
        <div
          class="clock_time_number"
          :style="{ transform: 'rotate(' + (i - 1) * -30 + 'deg)' }"
        >
          {{ ((i + 10) % 12) + 1 }}
        </div>
      </div>
      <div
        class="clock_hand clock_hand-minute"
        :class="{
          'clock_hand-edit': edit,
          'clock_hand-isMoving': movingHand === 'minute',
        }"
        :style="{ transform: 'rotate(' + minute * 6 + 'deg)' }"
        @mousedown="moveHand($event, 'minute')"
        @touchstart="moveHand($event, 'minute')"
      ></div>
      <div
        class="clock_hand clock_hand-hour"
        :class="{
          'clock_hand-edit': edit,
          'clock_hand-isMoving': movingHand === 'hour',
        }"
        :style="{ transform: 'rotate(' + (hour * 30 + minute / 2) + 'deg)' }"
        @mousedown="moveHand($event, 'hour')"
        @touchstart="moveHand($event, 'hour')"
      ></div>
    </div>
    <div class="clock_btns">
      <v-btn
        v-if="edit"
        :color="minutes ? 'pink' : ''"
        @click.stop="minutes = !minutes"
        class="clock_btn"
        size="x-large"
        >{{language === 'ja' ? 'ふん' : 'Minutes'}}</v-btn
      >
      <div class="clock_btns_spacer" />
      <v-btn
      v-if="edit"
        @click.stop="setTime([0, 0])"
        class="clock_btn"
        size="x-large"
        >{{language === 'ja' ? 'レセット' : 'Reset'}}</v-btn
      >
    </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'Clock',
  props: {
    time: {
      type: Array,
      default: () => []
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    minutes: false,
    centerX: 0,
    centerY: 0,
    movingHand: undefined
  }),
  computed: {
    value: {
      get () {
        return this.time
      },
      set (value) {
        this.$emit('update:time', value)
      }
    },
    hour () {
      return this.time[0] ? this.time[0] : 0
    },
    minute () {
      return this.time[1] ? this.time[1] : 0
    },
    width () {
      return this.$refs.clock.offsetWidth
    },
    ...mapGetters(['language'])
  },
  methods: {
    setTime (val) {
      this.value = val
      /*
      const x = e.layerX - this.width / 2
      const y = e.layerY - this.width / 2
      const deg = Math.atan2(y, x) * (180 / Math.PI)
      const timeDiff = Math.round(deg / 30)
      const time = (timeDiff + 15) % 12
      if (this.minutes) {
        this.value[1] = time * 5
      } else {
        this.value[0] = time
      } */
    },
    moveHand (e, hand) {
      e.preventDefault()
      const el = this.$refs.clock
      const rect = el.getBoundingClientRect()
      this.centerX = document.body.offsetWidth / 2
      this.centerY = rect.top + document.body.scrollTop + el.offsetWidth / 2
      document.addEventListener('mousemove', this.onMove, { passive: false })
      document.addEventListener('mouseup', this.onStopMove, { passive: false })
      document.addEventListener('touchend', this.onStopMove, {
        passive: false
      })
      document.addEventListener('touchmove', this.preventScroll, {
        passive: false
      })
      this.movingHand = hand
    },
    onMove (e) {
      let timeDiff, time
      const x = e.clientX - this.centerX
      const y = -(e.clientY - this.centerY)
      const deg = Math.atan2(y, x) * (180 / Math.PI)
      if (this.movingHand === 'minute') {
        timeDiff = Math.round(deg / 6)
        time = 59 - ((timeDiff + 45) % 60)
        this.value[0] = Math.round(this.value[0])
        if (time < 10 && this.value[1] > 50) {
          this.value[0]++
        }
        if (time > 50 && this.value[1] < 10) {
          this.value[0]--
        }
      } else {
        timeDiff = deg / 30
        const hour = (15 - timeDiff) % 12
        time = (hour % 1) * 60
        this.value[0] = Math.floor(hour)
      }
      this.value[1] = time
    },
    onStopMove () {
      this.movingHand = undefined
      document.removeEventListener('mousemove', this.onMove, {
        passive: false
      })
      document.removeEventListener('mouseup', this.onStopMove, {
        passive: false
      })
      document.removeEventListener('touchend', this.onStopMove, {
        passive: false
      })
      document.removeEventListener('touchmove', this.preventScroll, {
        passive: false
      })
    },
    preventScroll (e) {
      e.preventDefault()
      e.clientX = e.touches[0].pageX
      e.clientY = e.touches[0].pageY
      this.onMove(e)
    }
  }
}
</script>

<style lang="scss">
.clock {
  &_container {
    display: flex;
    flex-direction: column;
    align-items: center;

    &-isMoving {
      pointer-events: none;
    }
  }
  display: flex;
  flex-shrink: 0;
  flex-basis: auto;
  border-radius: 50%;
  position: relative;
  width: 100%;
  height: 100%;
  cursor: default;
  &-view {
    cursor: pointer;
  }
  &::before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    border: 1px solid #000;
    box-sizing: border-box;
    z-index: 1;
  }
  &::after {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
    width: 8%;
    height: 8%;
    top: 46%;
    left: 46%;
    background-color: #000;
    z-index: 4;
  }
  &_minutes {
    &_container {
      position: absolute;
      width: 112%;
      height: 112%;
      top: -6%;
      left: -6%;
      background-color: #e91e63;
      border-radius: 50%;
      border: 1px solid #000;
      transition: all 200ms;
      color: #fff;
      opacity: 0;
      &-view {
        opacity: 1;
      }
    }
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    transform-origin: center;
    &_number {
      position: relative;
      line-height: 1.3em;
      top: 0.8em;
      &-long {
        line-height: 1.6em;
        top: auto;
      }
    }
  }
  &_check {
    &_container {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1;
      background-color: #fff;
      border-radius: 50%;
    }
    position: absolute;
    left: calc(50% - 0.5px);
    top: 0;
    height: 50%;
    width: 1px;
    background-image: linear-gradient(
      #000 0%,
      #000 2.5%,
      transparent 2.5%,
      transparent 100%
    );
    transform-origin: bottom center;
    &-long {
      left: calc(50% - 1px);
      background-image: linear-gradient(
        #000 0%,
        #000 5%,
        transparent 5%,
        transparent 100%
      );
      width: 2px;
    }
  }
  &_time {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    transform-origin: center;
    z-index: 2;
  }
  &_hand {
    display: block;
    position: absolute;
    left: 45%;
    top: 8%;
    width: 10%;
    transform-origin: center 100%;
    bottom: 50%;
    transition: all 50ms opacity 200ms;
    z-index: 3;
    cursor: pointer;
    &-isMoving,
    &-edit:hover {
      opacity: 0.5;
    }
    &::before {
      content: "";
      display: block;
      height: 100%;
      position: absolute;
      background-color: #000;
      bottom: 0;
    }
    &-hour {
      top: 18%;
      &::before {
        left: 15%;
        right: 15%;
        border-radius: 20px;
        background-color: #2196f3;
      }
    }
    &-minute {
      &::before {
        left: 25%;
        right: 25%;
        border-radius: 16px;
        background-color: #e91e63;
      }
    }
  }
  &_btns {
    display: flex;
    padding: 48px 32px;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 32px;

    &_spacer {
      width: 50vw;
      max-width: 500px;
      min-width: 160px;
    }
  }
  &_btn {
    display: block;
    width: 140px;
    position: relative;
    z-index: 9;

    &.bg-pink {
      color: #fff;
    }
  }
}
</style>
