import { createStore } from 'vuex'

export default createStore({
  state: {
    language: 'ja'
  },
  mutations: {
    toggleLanguage (state) {
      state.language = state.language === 'ja' ? 'en' : 'ja'
    }
  },
  getters: {
    language: state => state.language
  },
  actions: {
  },
  modules: {
  }
})
